"use client";

import { useState } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";

export default function UserAgentLookup() {
  const [userAgent, setUserAgent] = useState("");
  const [result, setResult] = useState<string | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleLookup = () => {
    setError(null);
    setResult(null);

    if (!userAgent.trim()) {
      setError("Please enter a User-Agent string.");
      return;
    }

    try {
      const browserMatch = userAgent.match(/(Chrome|Firefox|Safari|Edge|Opera)[\/\s]?([\d\.]+)/i);
      const osMatch = userAgent.match(/\(([^)]+)\)/);
      const os = osMatch ? osMatch[1] : "Unknown OS";
      const browser = browserMatch ? `${browserMatch[1]} ${browserMatch[2]}` : "Unknown Browser";

      setResult(`Detected OS: ${os}\nDetected Browser: ${browser}`);
    } catch {
      setError("Failed to parse User-Agent string.");
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50 py-12 px-4">
      <div className="max-w-3xl mx-auto">
        <Card className="shadow-lg border border-gray-300">
          <CardHeader className="bg-gradient-to-r from-blue-600 to-purple-600 text-white rounded-t-lg p-6">
            <CardTitle className="text-3xl font-bold">üîç User-Agent Lookup Tool</CardTitle>
          </CardHeader>
          <CardContent className="space-y-6 p-6">
            <div>
              <label htmlFor="ua-input" className="block mb-2 font-semibold text-gray-700">
                Enter User-Agent String
              </label>
              <Input
                id="ua-input"
                type="text"
                value={userAgent}
                onChange={(e) => setUserAgent(e.target.value)}
                placeholder="Paste User-Agent string here"
                className="w-full border border-gray-300 rounded-md shadow-sm focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
              />
            </div>
            <Button onClick={handleLookup} className="w-full bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 rounded-md">
              Lookup
            </Button>
            {error && <p className="text-red-600 mt-2 font-semibold">{error}</p>}
            {result && (
              <pre className="bg-gray-100 p-4 rounded text-sm whitespace-pre-wrap font-mono">{result}</pre>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
